import { defineComponent, ref, watch } from 'vue';
import ListItem from '@/components/list-item/index.vue';
import { ElEmpty, ElSkeleton } from 'element-plus';
import { randomNum } from '@/utils';
export default defineComponent({
    name: 'ListComp',
    components: { ListItem: ListItem, ElSkeleton: ElSkeleton, ElEmpty: ElEmpty },
    props: {
        // 列表数据
        listData: {
            typeof: Array,
            default: [],
        },
        // 加载中
        isLoading: {
            type: Boolean,
            default: false,
        },
        // 列表条数
        limit: {
            type: Number,
            default: 20,
        },
    },
    setup: function (props, content) {
        // 列表数据
        var list = ref([]);
        watch(function () { return props.listData; }, function (val) {
            list.value = val;
        });
        // 加载中
        var loading = ref();
        watch(function () { return props.isLoading; }, function (val) {
            loading.value = val;
        });
        // 列表条数
        var listLength = ref(props.limit);
        watch(function () { return props.limit; }, function (val) {
            listLength.value = val;
        });
        // 查看详情
        var seeDetail = function (v) {
            content.emit('seeDetail', v);
        };
        return {
            list: list,
            loading: loading,
            listLength: listLength,
            randomNum: randomNum,
            seeDetail: seeDetail,
        };
    },
});
